"use client";

import { useState } from "react";
import DeleteConfirmModal from "../../../../components/ui/DeleteConfirmModal/DeleteConfirmModal";

export default function MyEventsGrid({ events = [] }) {
  const [deleteId, setDeleteId] = useState(null);

  if (!events.length) {
    return (
      <div className="text-center py-5">
        <h5 className="fw-bold">No events yet</h5>
        <p className="text-muted">Create your first event</p>
      </div>
    );
  }

  const handleDelete = () => {
    console.log("Delete event id:", deleteId);
    // ðŸ‘‰ call delete API here
    setDeleteId(null);
  };

  return (
    <>
      <div className="row g-4">
        {events.map((e) => (
          <div key={e.id} className="col-xl-3 col-lg-4 col-md-6">
            <div className="card h-100 shadow-sm border-0 rounded-4">
              <div className="card-body d-flex flex-column">

                {/* TITLE */}
                <h6
                  className="fw-semibold text-truncate mb-4"
                  title={e.title}
                >
                  {e.title || "Untitled Event"}
                </h6>

                {/* ACTIONS */}
                <div className="mt-auto d-flex gap-2">
                  <button className="btn btn-outline-primary btn-sm w-100">
                    Edit
                  </button>

                  <button
                    className="btn btn-outline-danger btn-sm w-100"
                    onClick={() => setDeleteId(e.id)}
                  >
                    Delete
                  </button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* CONFIRM MODAL */}
      <DeleteConfirmModal
        open={!!deleteId}
        onClose={() => setDeleteId(null)}
        onConfirm={handleDelete}
        title="Delete Event"
        message="Are you sure you want to delete this event?"
      />
    </>
  );
}
