"use client";

import { useState } from "react";
import DeleteConfirmModal from "../../../../components/ui/DeleteConfirmModal/DeleteConfirmModal";

export default function MyEventsList({ events = [] }) {
  const [deleteId, setDeleteId] = useState(null);

  if (!events.length) {
    return (
      <div className="text-center py-5">
        <h5 className="fw-bold">No events yet</h5>
        <p className="text-muted">Create your first event</p>
      </div>
    );
  }

  const handleConfirmDelete = () => {
    console.log("Delete event id:", deleteId);
    // ðŸ‘‰ DELETE API CALL HERE
    setDeleteId(null);
  };

  return (
    <>
      <div className="list-group list-group-flush">
        {events.map((e) => (
          <div
            key={e.id}
            className="list-group-item border-0 mb-3 shadow-sm rounded-4"
          >
            <div className="d-flex align-items-center justify-content-between gap-3">

              {/* TITLE */}
              <h6
                className="fw-semibold text-truncate mb-0"
                title={e.title}
                style={{ maxWidth: "70%" }}
              >
                {e.title || "Untitled Event"}
              </h6>

              {/* ACTIONS */}
              <div className="d-flex gap-2">
                <button className="btn btn-outline-primary btn-sm">
                  Edit
                </button>

                <button
                  className="btn btn-outline-danger btn-sm"
                  onClick={() => setDeleteId(e.id)}
                >
                  Delete
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* CONFIRM DELETE MODAL */}
      <DeleteConfirmModal
        open={!!deleteId}
        onClose={() => setDeleteId(null)}
        onConfirm={handleConfirmDelete}
        title="Delete Event"
        message="Are you sure you want to delete this event?"
      />
    </>
  );
}
